.LaunchesList {
  background: $almost-black;
  display: flex;
  flex-direction: column;
  @include basic_exp;
  text-transform: uppercase;
  flex: 1;
  &__header {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 100vw;
    height: 612px;
    flex: 1;
    background: url("../assets/img/mars.png") no-repeat center center;
    background-size: 64%; }
  &__header-wrapper {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 26em;
    margin-left: auto;
    margin-right: auto;
    height: 400px;
    width: 700px; }
  &__title {
    width: 700px;
    height: 200px;
    background: url("../assets/img/space_x_logo_bw_centered.png") no-repeat;
    background-position-x: 8em;
    background-position-y: center;
    background-size: 84%;
    margin: 0 auto; }
  &__subtitle {
    margin: -1.8em auto;
    @include basic-exp;
    letter-spacing: .75em;
    font-size: 1.7em;
    color: $cool-grey; }
  &__list {
    margin-top: 1.5em;
    display: flex;
    flex-direction: column-reverse; } }


.filterButtons {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 70vw;
  margin: 1.6em auto; }

.LaunchesList__wrapper {
  position: relative;
  margin: 4em 2.5em;
  min-height: 500px;
  &:after {
      content: "";
      position: absolute;
      z-index: 1;
      top: 0;
      bottom: 0;
      left: 50%;
      height: 100%;
      border: .5px dashed $cool-grey;
      transform: translate(-50%); } }

.css-snl7ld {
  position: fixed !important;
  top: 50vh;
  left: 47vw;
  z-index: 99;
  &:before {
    content: "";
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 500ms; } }
.error {
  display: flex;
  min-height: 500px;
  text-align: center;
  &__text {
    flex: 1;
    @include basic-exp;
    color: white; } }
